* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background-color: #141414;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-height: 100%;
  font-family: Inter, sans-serif;
  animation: 1s ease-in-out animate;
  display: flex;
  overflow: hidden;
}

.container {
  background-color: #1f1f1f;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  width: 335px;
  padding: 1.5rem 0;
  display: flex;
}

.container img {
  border-radius: 50%;
  width: 70px;
  height: 70px;
}

.container header h1 {
  color: #fff;
  margin-bottom: .5rem;
  font-size: 1.4rem;
}

.container header span {
  color: #c5f82a;
  font-size: 14px;
  font-weight: 600;
}

.container p {
  color: #ccc;
  font-size: 14px;
}

.container .links {
  text-align: center;
  flex-direction: column;
  gap: 1rem;
  width: 85%;
  display: flex;
}

.container .links a {
  color: #fff;
  background-color: #333;
  border-radius: 5px;
  padding: .8rem;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
}

.container .links a:hover {
  color: #1f1f1f;
  background-color: #c5f82a;
}

.container .links .github {
  animation: .3s ease-in animateLink;
}

.container .links .frontend {
  animation: .5s ease-in animateLink;
}

.container .links .linkedin {
  animation: .7s ease-in animateLink;
}

.container .links .twitter {
  animation: 1s ease-in animateLink;
}

.container .links .instagram {
  animation: 1.2s ease-in animateLink;
}

@keyframes animate {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes animateLink {
  0% {
    opacity: 0;
    width: 0;
  }

  100% {
    opacity: 1;
    width: 100%;
  }
}

@media (width >= 1280px) {
  .container {
    width: 370px;
    padding: 2rem 0;
  }

  .container .links {
    width: 80%;
  }
}

